<template>
	<div class="market-supervision-20" v-loading="loading" element-loading-spinner="iconfont icon-loading1" element-loading-background="rgba(0, 0, 0, 0.2)">
		<area-panel area-title="价格监测" titleLeft>
			<div class="sub-title" slot="subTitle">
				<el-select v-model="cropId" placeholder="请选择" slot="subTitle" class="vegetable-select two" filterable ref="cropSearch" @change="selectCrop">
					<el-option v-for="(item, index) in vegetableVariety" :key="index" :label="item.crop" :value="item.cropId"> </el-option>
				</el-select>
			</div>
			<div v-show="!loading" class="main-panel" slot="panelMain">
				<!-- 主区域 -->
				<div class="echart-container" ref="EContainer"></div>
			</div>
		</area-panel>
	</div>
</template>

<script>
import areaPanel from '@/components/overview/area-panel';
import {dateFormat, maxDecimalPlaces, areaLevelCorAreaId} from '@/common/fn';
import {getStorage} from '@/utils/storage';

const xData = ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'];
// echarts 超级变量含义 {a} series.name, {b} data.name {c} data.value {d} data%
const lineChartOption = {
	title: {
		show: false,
		text: '暂无数据',
		textStyle: {
			fontSize: 12,
			color: '#6DC1CB'
		},
		left: 'center',
		top: 'center'
	},
	grid: {
		show: true,
		borderColor: '#0D394A',
		bottom: 30,
		top: 40,
		right: 20
		// height: '65%'
	},
	tooltip: {
		show: true,
		trigger: 'axis',
		formatter(series) {
			const seriesList = series.map(seriesInfo => {
				return `${seriesInfo.seriesName}：${seriesInfo.data !== null ? maxDecimalPlaces({value: seriesInfo.data}) + '元/公斤' : '无'}`;
			});
			return `${series[0].axisValue}<br/>${seriesList.join('<br />')}`;
		},
		backgroundColor: '#043250',
		padding: [13, 14, 13, 11],
		textStyle: {
			fontSize: 12,
			color: '#9ED2D8'
		},
		confine: true
	},
	color: ['#DC6CFE', '#00E676', '#F6A82B', '#00D7E0'],
	legend: {
		show: true,
		type: 'plain',
		textStyle: {
			color: '#6DC1CB',
			fontSize: 12
		},
		backgroundColor: 'rgba(255, 255, 255, 0)',
		top: 3,
		right: 16,
		itemWidth: 6,
		itemHeight: 6,
		itemGap: 14,
		height: 20,
		icon: 'circle',
		borderColor: 'yellow'
		// data: [
		//   {
		//     name: '田头价',
		//     icon: `image://${require('images/excel-icon-a.png')}`
		//   },
		//   {
		//     name: '批发价',
		//     icon: `image://${require('images/excel-icon-b.png')}`
		//   },
		//   {
		//     name: '电商价',
		//     icon: `image://${require('images/excel-icon-c.png')}`
		//   },
		//   {
		//     name: '零售价',
		//     icon: `image://${require('images/excel-icon-d.png')}`
		//   }
		// ]
	},
	xAxis: {
		data: xData,
		boundaryGap: false,
		axisLabel: {
			formatter(label) {
				const dateFormatReg = /([\d]*)[年]([\d]*)[月]([\d]*)[日]/g;
				return label.replace(dateFormatReg, '$2月$3日');
			},
			textStyle: {
				color: '#6DC1CB',
				fontSize: 12
			},
			align: 'center'
		},
		axisTick: {
			show: false
		},
		axisLine: {
			show: true,
			lineStyle: {
				// color: '#0D394A'
				color: 'rgba(109,193,203,0.2)'
			}
		},
		z: 10
	},
	yAxis: {
		name: '(元/公斤)',
		nameTextStyle: {
			fontSize: 12,
			color: '#6DC1CB',
			padding: [4, 8, 5, 10]
		},
		axisLine: {
			show: false,
			lineStyle: {
				color: '#0D394A'
			}
		},
		axisTick: {
			show: false
		},
		splitLine: {
			show: true,
			lineStyle: {
				// color: '#0D394A'
				color: 'rgba(109,193,203,0.2)'
			}
		},
		axisLabel: {
			textStyle: {
				color: '#6DC1CB',
				fontSize: 12
			}
		}
	},
	series: [
		{
			name: '田头价',
			data: [],
			type: 'line',
			smooth: true,
			symbolSize: 0,
			connectNulls: true,
			areaStyle: {
				color: {
					type: 'linear',
					x: 0,
					y: 0,
					x2: 0,
					y2: 1,
					colorStops: [
						{
							offset: 0,
							color: 'rgba(233,128,251, 0.5)' // 0% 处的颜色
						},
						{
							offset: 1,
							color: 'rgba(233,128,251, 0)' // 100% 处的颜色
						}
					],
					global: false // 缺省为 false
				}
			},
			lineStyle: {
				color: '#DC6CFE'
			}
		},
		{
			name: '批发价',
			data: [],
			type: 'line',
			smooth: true,
			symbolSize: 0,
			connectNulls: true,
			areaStyle: {
				color: {
					type: 'linear',
					x: 0,
					y: 0,
					x2: 0,
					y2: 1,
					colorStops: [
						{
							offset: 0,
							color: 'rgba(0,230,118, 0.5)' // 0% 处的颜色
						},
						{
							offset: 1,
							color: 'rgba(0,230,118, 0)' // 100% 处的颜色
						}
					],
					global: false // 缺省为 false
				}
			},
			lineStyle: {
				color: '#00E676'
			}
		},
		{
			name: '零售价',
			data: [],
			type: 'line',
			smooth: true,
			symbolSize: 0,
			connectNulls: true,
			areaStyle: {
				color: {
					type: 'linear',
					x: 0,
					y: 0,
					x2: 0,
					y2: 1,
					colorStops: [
						{
							offset: 0,
							color: 'rgba(212, 167, 84, 0.5)' // 0% 处的颜色
						},
						{
							offset: 1,
							color: 'rgba(212, 167, 84, 0)' // 100% 处的颜色
						}
					],
					global: false // 缺省为 false
				}
			},
			lineStyle: {
				color: '#F6A82B'
			}
		},
		{
			name: '电商价',
			data: [],
			type: 'line',
			smooth: true,
			symbolSize: 0,
			connectNulls: true,
			areaStyle: {
				color: {
					type: 'linear',
					x: 0,
					y: 0,
					x2: 0,
					y2: 1,
					colorStops: [
						{
							offset: 0,
							color: 'rgba(0,162,255, 0.5)' // 0% 处的颜色
						},
						{
							offset: 1,
							color: 'rgba(0,162,255, 0)' // 100% 处的颜色
						}
					],
					global: false // 缺省为 false
				}
			},
			lineStyle: {
				color: '#00D7E0'
			}
		}
	]
};
const govGovernmentSubjectVO = getStorage('govGovernmentSubjectVO');
const vegetableVariety = getStorage('vegetableVariety');
// 当前年的时间格式 时间戳
const yesterdayTimestamp = new Date().setDate(new Date().getDate() - 1);
const curDate = dateFormat(yesterdayTimestamp, 'yyyy-MM-dd');
const prevDate = dateFormat(
	curDate
		.split('-')
		.map((date, i) => {
			let nDate = Number(date);
			if (i === 0) {
				nDate -= 1;
			}
			return nDate;
		})
		.join('/'),
	'yyyy-MM-dd'
);
export default {
	name: 'market-supervision-23',
	props: {
		farmAreaInfo: {
			type: Object,
			default() {
				return areaLevelCorAreaId();
			}
		}
	},
	data() {
		return {
			// 价格走势chart
			chart: null,
			// loading
			loading: false,
			// 市场价格类型列表
			priceTypeList: [
				{
					name: '田头价',
					id: 0
				},
				{
					name: '批发价',
					id: 1
				}
			],
			// 蔬菜品种下拉框
			vegetableVariety: [],
			// 蔬菜品种下拉框默认值
			cropId: null
		};
	},
	mounted() {
		this.initPage();
	},
	methods: {
		// 获取 田头价作物列表
		getTtjCropList() {
			return this.$ajax
				.post(this.$api.operate.ttjCropList, {
					pageNo: 1,
					pageSize: 10000
				})
				.then(({data: res}) => {
					if (res.respCode === 0) {
						return res.obj;
					}
					throw new Error(res);
				})
				.catch(e => {
					console.log(e);
					throw e;
				});
		},
		// 获取 田头价|批发价|电商价|零售价的价格趋势
		getCropPriceStatisticListWithAllPriceType() {
			return this.$ajax
				.post(this.$api.operate.cropPriceStatisticListWithAllPriceType, {
					provinceId: govGovernmentSubjectVO.provinceId,
					cropId: this.cropId,
					startDateStr: prevDate, // 开始时间[包含这一天] 格式:yyyy-MM-dd
					endDateStr: curDate, // 结束时间[包含这一天],格式:yyyy-MM-dd
					statisticType: 1 // 统计类型：1日、2周、3月、4年
				})
				.then(({data: res}) => {
					if (res.respCode === 0) {
						return res.obj;
					}
					throw new Error(res);
				})
				.catch(e => {
					throw e;
				});
		},

		// 处理 田头价作物列表
		async processTtjCropList() {
			try {
				const res = await this.getTtjCropList();
				this.vegetableVariety = res.list.map(item => {
					return {
						crop: item.cropName,
						cropId: item.id
					};
				});
				this.cropId = this.vegetableVariety[0].cropId;
			} catch (e) {
				console.log(e);
				throw e;
			}
		},
		// 处理 价格趋势数据
		async processCropPriceStatisticListWithAllPriceType() {
			try {
				const cropPriceStatisticList = await this.getCropPriceStatisticListWithAllPriceType();
				for (let i = cropPriceStatisticList.length - 1; i >= 0; i--) {
					const priceInfo = cropPriceStatisticList[i];
					const flag = priceInfo.ebusinessAveragePrice !== null || priceInfo.lotAveragePrice !== null || priceInfo.retailAveragePrice !== null || priceInfo.ttjAveragePrice !== null;
					if (flag) {
						break;
					}
					cropPriceStatisticList.pop();
				}
				return cropPriceStatisticList;
			} catch (e) {
				console.log(e);
				throw e;
			}
		},
		async updateChart() {
			try {
				const priceList = await this.processCropPriceStatisticListWithAllPriceType();
				// const xData = priceList.map(priceInfo => priceInfo.statisticDate);
				const splitPriceList = priceList.reduce(
					(acc, priceInfo) => {
						acc[0].push(priceInfo.statisticDate);
						acc[1].push(priceInfo.ttjAveragePrice);
						acc[2].push(priceInfo.lotAveragePrice);
						acc[3].push(priceInfo.retailAveragePrice);
						acc[4].push(priceInfo.ebusinessAveragePrice);
						return acc;
					},
					[
						[
							/*xAxis*/
						],
						[
							/*田头价*/
						],
						[
							/*批发价*/
						],
						[
							/*零售价*/
						],
						[
							/*电商价*/
						]
					]
				);
				// console.log(splitPriceList);
				this.chart.setOption({
					xAxis: {
						data: splitPriceList[0]
					},
					series: [{data: splitPriceList[1]}, {data: splitPriceList[2]}, {data: splitPriceList[3]}, {data: splitPriceList[4]}]
				});
			} catch (e) {
				console.log(e);
				throw e;
			}
		},

		// 页面初始化
		async initPage() {
			this.chart = this._initEcharts(this.eContainerRef, lineChartOption);
			await this.updateCropList();
			this.updateCropPrice();
			this.adaptationChart();
			window.addEventListener('resize', this.adaptationChart);
			this.$once('hook:beforeDestroy', function() {
				window.removeEventListener('resize', this.adaptationChart);
			});
		},

		// 更新作物价格趋势
		async updateCropPrice() {
			this.loading = true;
			await this.updateChart();
			this.loading = false;
		},

		// 更新作物列表
		async updateCropList() {
			this.vegetableVariety = [];
			await this.processTtjCropList();
		},

		// 选择作物
		selectCrop() {
			this.chart.setOption({
				series: [
					{
						data: []
					},
					{
						data: []
					},
					{
						data: []
					},
					{
						data: []
					}
				]
			});
			this.updateCropPrice();
			this.$nextTick(() => {
				this.$refs.cropSearch.blur();
			});
		},

		// 检测是否需要重新加载作物数据
		checkCropReload() {
			let cropTime = getStorage('cropTime');
			if (!cropTime) return true;
			cropTime = Number(cropTime);
			const updateMonth = new Date(cropTime).getMonth();
			const updateTime = new Date(cropTime).setMonth(updateMonth + 1, 1);
			const curEarlyMonth = new Date().getTime();
			return updateTime >= curEarlyMonth;
		},

		_initEcharts(el, option) {
			const chart = this.$echarts.init(el);
			chart.setOption(option);
			return chart;
		},

		adaptationChart() {
			const htmlWidth = document.documentElement.clientWidth || document.body.clientWidth;
			if (htmlWidth > 3000) {
				if (this.chart) {
					this.chart.setOption({
						tooltip: {
							textStyle: {
								fontSize: 14
							}
						},
						yAxis: {
							nameTextStyle: {
								fontSize: 14
							},
							axisLabel: {
								textStyle: {
									fontSize: 14
								}
							}
						},
						xAxis: {
							axisLabel: {
								textStyle: {
									fontSize: 14
								}
							}
						},
						legend: {
							textStyle: {
								fontSize: 16
							},
							itemWidth: 8,
							itemHeight: 8
						}
					});
				}
			} else {
				if (this.chart) {
					this.chart.setOption({
						tooltip: {
							textStyle: {
								fontSize: 12
							}
						},
						yAxis: {
							nameTextStyle: {
								fontSize: 12
							},
							axisLabel: {
								textStyle: {
									fontSize: 12
								}
							}
						},
						xAxis: {
							axisLabel: {
								textStyle: {
									fontSize: 12
								}
							}
						},
						legend: {
							textStyle: {
								fontSize: 12
							},
							itemWidth: 6,
							itemHeight: 6
						}
					});
				}
			}
		},
		refreshRender() {
			if (this.chart) {
				this.chart.resize();
			}
		}
	},
	computed: {
		eContainerRef() {
			return this.$refs.EContainer;
		}
	},
	components: {
		areaPanel
	}
};
</script>

<style scoped lang="scss">
@import '~styles/selectSkin_default.scss';
.market-supervision-20 {
	width: 100%;
	height: 100%;
	/deep/ .el-select {
		.el-input__inner {
			width: 93px;
			height: 30px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			padding: 0 25px 0 10px;
			.el-input__suffix {
				right: 0;
			}
			&::-webkit-input-placeholder {
				color: rgba(39, 145, 149, 0.5);
			}
			&:-moz-placeholder {
				/* Mozilla Firefox 4 to 18 */
				color: rgba(39, 145, 149, 0.5);
			}
			&::-moz-placeholder {
				/* Mozilla Firefox 19+ */
				color: rgba(39, 145, 149, 0.5);
			}
			&:-ms-input-placeholder {
				/* Internet Explorer 10+ */
				color: rgba(39, 145, 149, 0.5);
			}
		}
		+ .el-select {
			margin-left: 2px;
		}
	}
	.main-panel {
		width: 100%;
		height: 100%;
	}

	.sub-title {
		position: absolute;
		right: 15px;
	}
	.echart-container {
		height: 100%;
		width: 100%;
	}
}

/deep/ .el-loading-spinner {
	overflow: hidden;
	.iconfont {
		display: block;
		font-size: 40px;
		color: #00f6ff;
		animation: loading 1.25s linear infinite;
		margin-top: 24px;
		margin-left: 8px;
	}
}
</style>



